// Components
import BalloonMessageContainer from '@extension/components/balloon_message_container';
// Common
import { BalloonMessageCommands } from '@extension/common/commands';
import DWS from '@extension/ui/balloon_message/dws';
import AdvancedProtectionSignal from '@extension/ui/balloon_message/advanced_protection_signal';
import AdvancedProtectionSignalToast from '@extension/ui/balloon_message/advanced_protection_signal_toast';
import BackgroundIPC from '@extension/common/background_ipc';

export default class BalloonMessageUIHandler
{
  constructor()
  {
    this.componentMap = {
      [BalloonMessageCommands.DWS]: {
        template: {
          htmlPath: 'html/balloon_message/dws.html',
          cssPath: 'css/dws.css',
        },
        styles: {
          right: '70px',
          top: '8px',
        },
        Component: DWS,
      },
      [BalloonMessageCommands.ADVANCED_PROTECTION_SIGNAL]: {
        template: {
          htmlPath: 'html/balloon_message/advanced_protection_signal.html',
          cssPath: 'css/advanced_protection_signal.css',
        },
        styles: {
          right: '15px',
        },
        Component: AdvancedProtectionSignal,
      },
      [BalloonMessageCommands.ADVANCED_PROTECTION_SIGNAL_TOAST]: {
        template: {
          htmlPath: 'html/balloon_message/advanced_protection_signal_toast.html',
          cssPath: 'css/advanced_protection_signal_toast.css',
        },
        styles: {
          right: '15px',
        },
        Component: AdvancedProtectionSignalToast,
      },
    };
  }

  async processMessage(payload)
  {
    const { subcommand, componentData } = payload;
    if (!subcommand) return;

    const componentDetails = this.componentMap[subcommand];
    if (!componentDetails) return;

    const { template, styles, Component } = componentDetails;

    // Pass the Background generated Secret to all balloon messages
    const bkGlobals = await BackgroundIPC.getBkGlobals();
    const waImageSecret = bkGlobals.WA_IMAGE_SECRET;
    template.waImageSecret = waImageSecret;

    this.balloonMessageContainer = new BalloonMessageContainer(styles, waImageSecret);

    const success = await this.balloonMessageContainer.init(template, componentData);
    if (!success) return;

    new Component(componentData, this.balloonMessageContainer).init();
  }
}
