export class CommonUtils
{
  static combineExceptions(trainingData)
  {
    let result = [];
    for (const formName in trainingData)
    {
      const domains = trainingData[formName];
      for (const domain in domains)
      {
        const exceptions = domains[domain];
        result = result.concat(exceptions);
      }
    }

    return result;
  }

  static validateEmailFormat(email)
  {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    return re.test(email);
  }
}

export class FDBoolean
{
  static toBit(bool)
  {
    return bool ? 1 : 0;
  }
}

export class FDWindow
{
  static isBrowser()
  {
    return chrome || window.safari;
  }

  static getFavicon()
  {
    const links = document.getElementsByTagName('link');
    let favicon = '';
    for (let i = 0; i < links.length; i++)
    {
      const item = links[i];
      if (item.getAttribute('rel') === 'icon' || item.getAttribute('rel') === 'shortcut icon')
      {
        favicon = item.href;
      }
      if (item.getAttribute('rel') === 'apple-touch-icon')
      {
        favicon = item.href;
        break;
      }
    }
    return favicon;
  }

  static getOriginUrlForTraining()
  {
    if (window.location.ancestorOrigins !== undefined
      && window.location.ancestorOrigins.length > 0)
    {
      return window.location.ancestorOrigins[0];
    }

    return window.location.href;
  }
}

export class FDHtmlElement
{
  static isHiddenFromScreen(el)
  {
    if (el.hidden === true)
    {
      return true;
    }
    if (el.tabIndex === -1)
    {
      return true;
    }
    if (el.getAttribute('aria-hidden') === true || el.getAttribute('aria-hidden') === 'true')
    {
      return true;
    }
    const styles = window.getComputedStyle(el);
    if (styles.visibility === 'hidden' || styles.display.includes('none') || styles.opacity === '0')
    {
      return true;
    }
    const element = el.getBoundingClientRect();
    const documentRect = document.documentElement.getBoundingClientRect();
    if (element.top + element.height < documentRect.top)
    {
      return true;
    }
    if (element.left > Math.max(documentRect.right, documentRect.width))
    {
      return true;
    }
    if (element.width === 0 || element.height === 0)
    {
      return true;
    }
    return false;
  }

  static hasCaptcha(el)
  {
    const elements = Array.prototype.slice.call(el.querySelectorAll("[id*='captcha']"));
    for (const el of elements)
    {
      if (FDHtmlElement.isHiddenFromScreen(el) === false)
      {
        return true;
      }
    }
    return false;
  }

  static triggerValueChange(el)
  {
    const inputEvent = new Event('input', { bubbles: true, cancelable: true });
    el.dispatchEvent(inputEvent);
    const changeEvent = new Event('change', { bubbles: true, cancelable: true });
    el.dispatchEvent(changeEvent);
    const keyupEvent = new Event('keyup', { bubbles: true });
    el.dispatchEvent(keyupEvent);
    el.blur();
  }

  static passwordFieldsCount(el)
  {
    const elements = el.getElementsByTagName('input');
    let result = 0;
    for (let i = 0; i < elements.length; i++)
    {
      const type = elements[i].getAttribute('type') ? elements[i].getAttribute('type').toLowerCase() : '';
      if (type === 'password' && FDHtmlElement.isHiddenFromScreen(elements[i]) === false)
      {
        result++;
      }
    }
    return result;
  }

  static inputFieldsCount(el)
  {
    const array = el.getElementsByTagName('input');
    let result = 0;
    for (let i = 0; i < array.length; i++)
    {
      const type = array[i].getAttribute('type') ? array[i].getAttribute('type').toLowerCase() : '';
      if (type !== 'hidden'
            && type !== 'checkbox'
            && type !== 'submit'
            && type !== 'button'
            && type !== 'password'
            && FDHtmlElement.isHiddenFromScreen(array[i]) === false)
      {
        result++;
      }
    }
    return result;
  }

  static getParentForm(el)
  {
    if (el.closest)
    {
      const form = el.closest('form');
      return form || undefined;
    }

    let parent = el.parentElement;
    while (!(parent instanceof HTMLFormElement) && parent)
    {
      parent = parent.parentElement;
    }
    return parent;
  }

  /**
   * Returns closest parent that is of position type "relative" or "absolute" or the body itself.
   * @param {HTMLElement} - The html element
   */
  static getOffsetDiv(el)
  {
    let offsetParentDiv;

    if (el.offsetParent && el.offsetParent instanceof HTMLDivElement)
    {
      offsetParentDiv = el.offsetParent;
    }

    return offsetParentDiv;
  }

  static intersectWithClientRect(el, zone)
  {
    const elementRect = el.getBoundingClientRect();
    return !(elementRect.left > zone.right
        || elementRect.right < zone.left
        || elementRect.top > zone.bottom
        || elementRect.bottom < zone.top);
  }

  static performRealMouseClick(el)
  {
    const realMouseClick = new MouseEvent(
      'click',
      {
        view: window,
        bubbles: true,
        cancellable: true,
        detail: 1
      }
    );
    el.dispatchEvent(realMouseClick);
  }

  static performEnterKeyPress(el)
  {
    const enterKeyPress = new KeyboardEvent(
      'keypress',
      {
        altKey: false,
        bubbles: true,
        cancelBubble: false,
        cancelable: true,
        charCode: 0,
        code: 'Enter',
        composed: true,
        ctrlKey: false,
        currentTarget: null,
        defaultPrevented: true,
        detail: 0,
        eventPhase: 0,
        isComposing: false,
        isTrusted: true,
        key: 'Enter',
        keyCode: 13,
        location: 0,
        metaKey: false,
        repeat: false,
        returnValue: false,
        shiftKey: false,
        type: 'keypress',
        which: 13
      }
    );
    el.dispatchEvent(enterKeyPress);
  }

  /**
   * Gets the FDProcessedID of an element if it exists, returns null otherwise.
   * @param {HTMLElement} el
   * @returns string value of FDProcessedID
   */
  static getFDProcessedID(el)
  {
    return el.getAttribute('FDProcessedID');
  }

  /**
   * Tells whether or not the element has the FDProcessedID attribute
   * @param {HTMLElement} el
   * @returns boolean
   */
  static hasFDProcessedID(el)
  {
    return el.hasAttribute('FDProcessedID');
  }

  /**
   * Adds a fd processed ID to an element and then returns the new id for the element.
   * If the element already has an ID it will just return the pre-existing one.
   * @param {HTMLElement} el
   * @returns string value of FDProcessedID
   */
  static addOrGetFDProcessedID(el)
  {
    if (FDHtmlElement.hasFDProcessedID(el))
    {
      return FDHtmlElement.getFDProcessedID(el);
    }

    const fdpid = Math.random().toString(36).substring(7);
    el.setAttribute('FDProcessedID', fdpid);
    return fdpid.toString(10);
  }
}
