export default class ScriptBase
{
  constructor(pingCommand)
  {
    this.pingCommand = pingCommand;
    this.basePingListener();
  }

  basePingListener(callback = null)
  {
    chrome.runtime.onMessage.addListener((request, sender, responseCb) =>
    {
      if (sender.id !== chrome.runtime.id)
      {
        return;
      }

      const { command } = request;
      if (command === this.pingCommand)
      {
        responseCb({ content: true });
        if (typeof callback === 'function') callback();
      }
    });
  }
}
