import { extension } from '@extension/common/webextension';
import HttpService from '@extension/common/http_service';
import BackgroundIPC from '@extension/common/background_ipc';
import { Logger } from '@extension/common/logger';
/////////////////////////////////
////////
//////////////////////////////////////////////////////////
//////////

export default class UIContentUtils
{
  /**
   * Load HTML templates into Shadow Dom
   * @param {object} properties                 - properties used by the host node
   * @param {object} properties.hostStyles      - css object to be applied on the host node
   * @param {object} baseTemplate               - main template to render
   * @param {string} baseTemplate.htmlPath      - path to the html file
   * @param {string} baseTemplate.cssPath       - path to the css file
   * @param {object} childTemplate              - (optional) inner template to render (if one is provided)
   * @param {string} childTemplate.htmlPath     - path to the html file
   * @param {string} childTemplate.cssPath      - path to the css file
   * @param {string} childTemplate.containerId  - append the html into this containerId from base html path
   */
  static async loadTemplate(properties, baseTemplate, childTemplate = null)
  {
    const { hostStyles } = properties;

    // Create and attach shadow DOM node to regular DOM
    const hostNode = document.createElement('div');
    for (const property in hostStyles)
    {
      hostNode.style[property] = hostStyles[property];
    }

    const bkGlobals = await BackgroundIPC.getBkGlobals();

    const mode = bkGlobals.shadowMode;

    const shadowRoot = hostNode.attachShadow({ mode });

    // Handle base template
    await UIContentUtils.populateContainer(baseTemplate, shadowRoot);

    // Handle child template (if one is provided)
    if (childTemplate) await UIContentUtils.populateContainer(childTemplate, shadowRoot);

    document.body.appendChild(hostNode);
    return { hostNode, shadowRoot };
  }

  static async populateContainer(templateData, shadowRoot)
  {
    const { htmlPath, cssPath, containerId, waImageSecret } = templateData;
    const template = await UIContentUtils.getTemplate(htmlPath, cssPath, waImageSecret);
    const container = containerId ? shadowRoot.querySelector(`#${containerId}`) : shadowRoot;
    container.appendChild(template);
  }

  static async reloadTemplate(templateData, baseTemplateData, shadowRoot)
  {
    // remove all data within the shadow root
    shadowRoot.innerHTML = '';

    // repopulate base container (if one is provided)
    if (baseTemplateData) await UIContentUtils.populateContainer(baseTemplateData, shadowRoot);

    // repopulate container
    await UIContentUtils.populateContainer(templateData, shadowRoot);
  }

  static async getTemplate(htmlPath, cssPath = null, waImageSecret)
  {
    const httpService = new HttpService();
    const template = document.createElement('template');

    // Grab markup and inject into the base template
    const responseText = await httpService.sendHttpRequest({ url: extension.runtime.getURL(htmlPath) });
    const htmlText = await responseText.text();

    // Grab styles and inject into the base template as a new style tag
    let stylesText = '';
    if (cssPath)
    {
      const responseStyles = await httpService.sendHttpRequest({ url: extension.runtime.getURL(cssPath) });
      stylesText = await responseStyles.text();
///////////////////////////////////////
////////////////////////////////////////////////////////////////
////////////////
////////////////////////////////////////////////////////////////
      stylesText = `<style>${stylesText}</style>`;
////////////////
    }
    let cleanHTML = '';
    // Wrapper template element
/////////////////////////////////////
///////////////////////////////////////
//////////////
//////////////////////////////////////////////////////////////
    cleanHTML = htmlText;
//////////////
    template.innerHTML = `${stylesText}${cleanHTML}`;

    // Resolve images
    for (const image of template.content.querySelectorAll('img[x-mcsrc]'))
    {
      const source = image.getAttribute('src');
      const secretParam = `?secret=${waImageSecret}`;
      image.src = extension.runtime.getURL(source + secretParam);
    }

    return template.content.cloneNode(true);
  }

  static async initFontOnDocument()
  {
    // Add custom typefaces and inject into the main document because shadow DOM does not load custom fonts.
    // For example Google Fonts must be added to the main document (light DOM) for typeface to work.
    try
    {
      // Poppins
      const poppinsUrl = extension.runtime.getURL('fonts/Poppins-Regular.ttf');
      const poppinsFont = new FontFace('mcafeePoppins', `url(${poppinsUrl})`);
      const poppinsPromise = poppinsFont.load(); // wait for font to be loaded

      // OpenSans
      const openSansUrl = extension.runtime.getURL('fonts/OpenSans-Regular.ttf');
      const openSansFont = new FontFace('mcafeeOpenSans', `url(${openSansUrl})`);
      const openSansPromise = openSansFont.load(); // wait for font to be loaded

      const [poppins, openSans] = await Promise.all([poppinsPromise, openSansPromise]);
      document.fonts.add(poppins); // add font to document
      document.fonts.add(openSans); // add font to document
    }
    catch (error)
    {
      Logger.error(error);
    }
  }

  static fadeIn(element)
  {
    element.style.opacity = 0;
    element.style.display = 'block';

    function fade()
    {
      const opacity = +element.style.opacity + 0.1;
      if (opacity < 1)
      {
        element.style.opacity = opacity;
        window.requestAnimationFrame(fade);
      }
    }

    fade();
  }

  static fadeOut(element, cb)
  {
    let stopTransition = false;
    element.style.opacity = 1;

    function fade()
    {
      // Sometimes the event queue isn't all cleared in time for fadeIn to begin
    // and a delayed invocation causes an infinite recursion battle between fadeIn & fadeOut
    // This flag acts as an official kill switch.
      if (stopTransition) return;

      const opacity = +element.style.opacity - 0.1;
      if (opacity <= 0)
      {
        stopTransition = true;
        element.style.display = 'none';
        cb();
      }
      else
      {
        element.style.opacity = opacity;
        window.requestAnimationFrame(fade);
      }
    }

    fade();
  }
}
