// Utilities
import UIContentUtils from '@extension/utilities/ui_content_utils';
import { Globals } from '@core-engine/globals';
import BackgroundIPC from '@extension/common/background_ipc';

/**
 * Base class handling the sidebar action panel markup, styles and event listeners.
 */
export default class ShadowDomContainer
{
  constructor(hostStyles, baseTemplate = null)
  {
    this.closing = false; // flag to indicate ui is closing
    this.showing = false; // flag to indicate ui is showing
    this.animateReload = true; // flag to animate slide out/in when reloading content
    this.allowReplace = true; // flag to allow content to be replaced or not
    this.componentData = null; // variable used for preventing same content to be re-rendered
    this.template = null; // variable used for preventing same template to be re-rendered (used along with allowReplace)

    this.hostNode = null;
    this.shadowRoot = null;
    this.hostStyles = hostStyles;
    this.baseTemplate = baseTemplate;

    // Exposed functions for extendee to use
    this.showContainer = this.showContainer.bind(this);
    this.closeContainer = this.closeContainer.bind(this);
    this.query = this.query.bind(this);
    this.clickListener = this.clickListener.bind(this);

    // feature
    this.bgGrayout = false; // for adding a background grayout overlay on top of the page
    this.bgGrayoutElement = null;
  }

  /**
   * Load HTML templates into Shadow Dom
   * @param {object}  template
   * @param {string}  template.htmlPath       - path to the html file
   * @param {string}  template.cssPath        - path to the css file
   * @param {string}  template.containerId    - (optional): append the html into this containerId from base html path
   * @param {bool}    template.allowReplace   - (default = true) set to false content cannot be replaced unless the template is different
   * @param {bool}    template.animateReload  - (default = true) when reloading the content, slide out/in when changing the content
   * @param {object}  template.componentData  - (default == null) - used mainly to avoid rendering same component
   */
  async initContainer(template, componentData = null)
  {
    // Prevent rendering UI if ui is closing or already showing
    if (this.closing) return false;

    // Also prevent if allowReplace=false and the template is the same
    if (this.showing && (this.allowReplace === false && ShadowDomContainer.isSameObject(template, this.template))) return false;

    // Ignore content that has been shown
    if (this.componentData && JSON.stringify(this.componentData) === JSON.stringify(componentData)) return false;

    this.componentData = componentData;

    // Set new behavior for the container
    const { allowReplace = true, animateReload = true } = template;
    this.allowReplace = allowReplace;
    this.animateReload = animateReload;
    this.showing = true;

    // If the host node has been created and allowReplace is true
    // then we can just reload the sidebar with new content
    if (this.hostNode && allowReplace)
    {
      await this.reloadContainer(template);
      return true;
    }

    // This is to handle parent/child template between containers which has a parent (eg. sidebar) and containers without one (eg. topbar)
    let childTemplate = { ...template };
    this.template = childTemplate;
    if (this.baseTemplate === null)
    {
      this.baseTemplate = childTemplate;
      childTemplate = null;
    }

    const properties = { hostStyles: { ...this.hostStyles.DEFAULT, ...this.hostStyles.HIDE } };
    const { hostNode, shadowRoot } = await UIContentUtils.loadTemplate(properties, this.baseTemplate, childTemplate);
    this.hostNode = hostNode;
    this.shadowRoot = shadowRoot;

    Globals.bkGlobals = await BackgroundIPC.getBkGlobals();

    return true;
  }

  static isSameObject(object1 = null, object2 = null)
  {
    if (object1 === null && object2 === null) return true;
    return object1 && object2 && JSON.stringify(object1) === JSON.stringify(object2);
  }

  async reloadContainer(template, animateReload = this.animateReload)
  {
    // On reload, close the sidebar (if animate = true)
    // prevent re-rendering during reload
    await this.closeContainer(false, animateReload);

    // Load new template
    this.template = template;
    await UIContentUtils.reloadTemplate(template, this.baseTemplate, this.shadowRoot);
  }

  setBackgroundOverlay(show)
  {
    const body = document.querySelector('body');

    if (show)
    {
      // Grayout the background
      const styles = 'display:none; width: 100%; height: 100%; position: fixed; top: 0px; left: 0px; z-index: 99999; background: rgba(0, 0, 0, 0.3);';
      this.bgGrayoutElement = document.createElement('div');
      this.bgGrayoutElement.style.cssText = styles;

      body.append(this.bgGrayoutElement); // Add the grayout
      body.style.overflow = 'hidden'; // hide the scrollbar
      UIContentUtils.fadeIn(this.bgOverlay);
    }
    else
    {
      if (this.bgGrayoutElement) UIContentUtils.fadeOut(this.bgGrayoutElement, () => {});
      body.style.overflow = 'auto'; // re-show the scrollbar
    }
  }

  showBackgroundGrayout(bgGrayout)
  {
    this.bgGrayout = bgGrayout;
    if (!bgGrayout) return;

    // Grayout the background
    const styles = 'display:none; width: 100%; height: 100%; position: fixed; top: 0px; left: 0px; z-index: 99999; background: rgba(0, 0, 0, 0.3);';
    this.bgGrayoutElement = document.createElement('div');
    this.bgGrayoutElement.style.cssText = styles;

    const body = document.querySelector('body');
    body.append(this.bgGrayoutElement); // Add the grayout
    body.style.overflow = 'hidden'; // hide the scrollbar
    UIContentUtils.fadeIn(this.bgGrayoutElement);
  }

  hideBackgroundGrayOut()
  {
    if (this.bgGrayoutElement && this.bgGrayout)
    {
      UIContentUtils.fadeOut(this.bgGrayoutElement, () => {});
      document.querySelector('body').style.overflow = 'auto'; // re-show the scrollbar
      this.bgGrayout = false;
    }
  }

  showContainer(options = null)
  {
    const bgGrayout = options && options.bgGrayout;
    this.showBackgroundGrayout(bgGrayout);
    for (const property in this.hostStyles.SHOW)
    {
      this.hostNode.style[property] = this.hostStyles.SHOW[property];
    }
  }

  closeContainer(clear = true, animate = true)
  {
    return new Promise((resolve) =>
    {
      // Handle background grayout removal
      this.hideBackgroundGrayOut();

      // Protect from multiple close triggers
      if (this.closing)
      {
        resolve();
        return;
      }

      this.closing = true;

      const close = () =>
      {
        this.closing = false;
        this.showing = false;

        // Clear when all content within the dom should
        // be reset, happens when triggering from UI buttons
        if (clear)
        {
          this.hostNode.remove();
          this.hostNode = null;
          this.componentData = null;
          this.template = null;
          this.shadowRoot = null;
          this.baseTemplate = null;
        }
      };

      if (!animate)
      {
        close();
        resolve();
        return;
      }

      // Hide action panel with animation
      const styleObj = { ...this.hostStyles.DEFAULT, ...this.hostStyles.HIDE };
      for (const property in styleObj)
      {
        this.hostNode.style[property] = styleObj[property];
      }

      setTimeout(() => resolve(close()), 500);
    });
  }

  query(id)
  {
    return this.shadowRoot.querySelector(id);
  }

  querySelector(selector)
  {
    return this.shadowRoot.querySelector(selector);
  }

  querySelectorAll(selector)
  {
    return this.shadowRoot.querySelectorAll(selector);
  }

  clickListener(id, callback)
  {
    const element = this.shadowRoot.querySelector(id);
    element && element.addEventListener('click', (event) =>
    {
      if (Globals.bkGlobals.isTrustCheckRequired && !event.isTrusted)
      {
        return;
      }
      callback(event);
    });
  }
}
