export default class HandlePromise
{
  static handlePromiseMessage(message, customCallback = null)
  {
    const defaultCallback = (resolve, reject, data) =>
    {
      if (chrome.runtime.lastError) reject(chrome.runtime.lastError.message);
      resolve(data);
    };

    return new Promise((resolve, reject) =>
    {
      chrome.runtime.sendMessage(message, (data) =>
      {
        if (typeof customCallback === 'function') customCallback(resolve, reject, data);
        else defaultCallback(resolve, reject, data);
      });
    });
  }
}
