import { BrowserType, BuildSettings } from '@extension/common/constants';

class NativeSettingsPreprocess
{
  static PreprocessSecureSearchRegEx(value)
  {
    return value.trim().split('\n');
  }

  static getBrowserSuffix()
  {
    const BROWSER_SUFFIX = {
      CH: '_ch',
      FF: '_ff',
      IE: '_ie',
      EDGE: '_ed',
    };

    const { CH, FF, EDGE } = BROWSER_SUFFIX;
    const suffixBrowserMap = {};
    suffixBrowserMap[BrowserType.EDGE] = EDGE;
    suffixBrowserMap[BrowserType.FIREFOX] = FF;
    suffixBrowserMap[BrowserType.CHROME] = CH;

    return suffixBrowserMap[BuildSettings.BROWSER_TYPE];
  }
}

export const SCOPE = {
  SYSTEM: 0,
  USER: 1,
};
const { SYSTEM, USER } = SCOPE;

export const SETTINGS_TYPE = {
  NONE: 0,
  INT: 1,
  INT64: 2,
  BOOL: 3,
  STRING: 4,
};

