export class CommonTelemetry
{
  static toColorTelemetry(color)
  {
    let colorStr = TelemetryColor.UNKNOWN;
    switch (color)
    {
      case Color.RED:
        colorStr = TelemetryColor.RED;
        break;
      case Color.YELLOW:
        colorStr = TelemetryColor.YELLOW;
        break;
    }
    return colorStr;
  }

  static toTelemetryBlockState(blockState)
  {
    let blockStateStr = TelemetryBlockState.UNKNOWN;
    switch (blockState)
    {
      case BlockState.GREEN:
        blockStateStr = TelemetryBlockState.GREEN;
        break;
      case BlockState.YELLOW:
        blockStateStr = TelemetryBlockState.YELLOW;
        break;
      case BlockState.RED:
        blockStateStr = TelemetryBlockState.RED;
        break;
      case BlockState.TYPOSQUATTING:
        blockStateStr = TelemetryBlockState.TYPOSQUATTING;
        break;
      case BlockState.PHISHING:
        blockStateStr = TelemetryBlockState.PHISHING;
        break;
    }
    return blockStateStr;
  }

  static toTelemetrySettingTab(tabName)
  {
    let tabNameStr = SETTING_TABS.PREFERENCES;
    switch (tabName)
    {
      case FEATURE_TAB_ID.WHITELIST:
        tabNameStr = SETTING_TABS.WHITELIST;
        break;
      case FEATURE_TAB_ID.STATISTICS:
        tabNameStr = SETTING_TABS.STATS;
        break;
      case FEATURE_TAB_ID.ABOUT:
        tabNameStr = SETTING_TABS.ABOUT;
        break;
    }
    return tabNameStr;
  }
}
