export const SearchAnnotationActions = {
  GET: 'GET',
  SET: 'SET',
};

export const AnnotationOptions = {
  NONE: 'NONE',
  ONLY_SECURE_SEARCH: 'ONLY_SECURE_SEARCH',
  ALL: 'ALL',
  * [Symbol.iterator]() // Override the iterable protocol to allow spreading of keys (...AnnotationOptions)
  {
    for (const key of Object.keys(this))
    {
      yield key;
    }
  }
};

