// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
// Common
import { BuildSettings } from '@extension/common/constants';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
// Foreground
import { ForegroundGlobals } from '@extension/ui/foreground/foreground_globals';

/**
 * The different log levels considered when detecting / reporting errors in WebAdvisor
 * Note that a higher level means that more logs will be printed. (ie, level 3 will print level 2 and 1)
 */
export const LogLevels = {
  NONE: 0,
  INFO: 1,
  ERROR: 2,
  WARN: 3,
  DEBUG: 4,
};

/**
 * The different log types
 */
export const LogTypes = {
  INFO: 1,
  ERROR: 2,
  WARN: 3,
  DEBUG: 4,
};

/**
 * Process type to distinguish which process called the logger
 */
export const ProcessTypes = {
  BACKGROUND: 'BACKGROUND',
  CONTENT: 'CONTENT',
  TELEMETRY: 'TELEMETRY',
  SMART_NATIVE_CLIENT: 'SMART_NATIVE_CLIENT'
};

export const ProcessStyleMap = {
  DEFAULT: 'color: #000000; font-weight: normal; font-style:normal; background: #FFFFFF;',
  BACKGROUND: 'color: #8D0DBA; font-weight: bold; background: #FFFFFF;',
  CONTENT: 'color: #F54A26; font-weight: bold; background: #FFFFFF;',
  TELEMETRY: 'color: #147831; font-weight: bold; background: #FFFFFF;',
  SMART_NATIVE_CLIENT: 'color: #004d7c; font-weight: bold; background: #FFFFFF;'
};

// *****************************************************************************
// Set "ExtensionLogging" = 1 in registry at the same place we set LogLevel
// will turn on native logging. This will log everything including INFO and ERR.
// *****************************************************************************
// *****************************************************************************
// Type "chrome.storage.local.set({'EXTENSION_LOGGING': 3});" into the background
// page console to forcefully turn on or turn off the logs inside extension
// chrome = chrome/browser, # = the log level from 0 to 4
// *****************************************************************************
export class Logger
{
  static processLog(message, logType, process = undefined)
  {
    if (logType === LogLevels.NONE)
    {
      return;
    }

    // verify that process is a valid option, else set it undefined
    if (process && !Object.values(ProcessTypes).includes(process))
    {
      process = undefined;
    }

    // Checking whether native logging is enabled or not.
    chrome.storage.local.get(LocalStorageVariables.USE_NATIVE_LOGGER.name, (useNativeLogger) =>
    {
      if (useNativeLogger[LocalStorageVariables.USE_NATIVE_LOGGER.name] === true)
      {
        if (!process)
        {
          // Check if the current code is being ran in the background or foreground
          process = isBackground() ? ProcessTypes.BACKGROUND : ProcessTypes.CONTENT;
        }
        const details = {
          processType: process,
          browserType: BuildSettings.BROWSER_TYPE,
          extensionType: 'wa',
          logType,
          message
        };

        BackgroundGlobals.messageDispatcher && BackgroundGlobals.messageDispatcher.logger(details);
      }
    });

    // log within extension
    consoleLog(message, logType, process);
  }

  /**
   * Set the native Logging status
   * @param {*} state
   */
  static setNativeLogging(state)
  {
    BackgroundGlobals.useNativeLogger = state;
    chrome.storage.local.set(LocalStorageVariables.USE_NATIVE_LOGGER, state);
  }

  // Need to include processType for telemetry logging only.
  static log(message, processType = undefined)
  {
    Logger.processLog(message, LogLevels.INFO, processType);
  }

  static error(message, processType = undefined)
  {
    Logger.processLog(message, LogLevels.ERROR, processType);
  }

  static warn(message, processType = undefined)
  {
    Logger.processLog(message, LogLevels.WARN, processType);
  }

  static debug(message, processType = undefined)
  {
    Logger.processLog(message, LogLevels.DEBUG, processType);
  }
}

const consoleLog = async (message, logType, process = undefined) =>
{
  const logLevel = await getLogLevel();
  if (logLevel === LogLevels.NONE)
  {
    return;
  }

  const processType = process || (isBackground() ? ProcessTypes.BACKGROUND
    : ProcessTypes.CONTENT);
  const nowDate = new Date();
  const logMessage = `%c[${processType} ${nowDate.toLocaleString([], { hour: '2-digit', minute: '2-digit', hour12: true })}]: %c${message}`;

  const styleRegular = ProcessStyleMap.DEFAULT;
  let hightlight = ProcessStyleMap[processType];

  if (!hightlight)
  {
    hightlight = styleRegular;
  }

  if (logLevel >= LogLevels.ERROR && logType === LogTypes.ERROR)
  {
    console.error(logMessage, styleRegular, styleRegular); // eslint-disable-line no-console
  }

  if (logLevel >= LogLevels.INFO && logType === LogTypes.INFO)
  {
    console.log(logMessage, hightlight, styleRegular); // eslint-disable-line no-console
  }

  if (logLevel >= LogLevels.WARN && logType === LogTypes.WARN)
  {
    const uniqueHighlight = 'color: #FFA500; font-family: sans-serif; font-weight: bolder; text-shadow: #000 1px 1px;';
    console.log(`%cWARN - ${logMessage}`, uniqueHighlight, hightlight, styleRegular); // eslint-disable-line no-console
  }

  if (logLevel >= LogLevels.DEBUG && logType === LogTypes.DEBUG)
  {
    const uniqueHighlight = 'color: #FF33D7; font-family: sans-serif; font-weight: bolder; text-shadow: #000 1px 1px;';
    console.log(`%cDEBUG - ${logMessage}`, uniqueHighlight, hightlight, styleRegular); // eslint-disable-line no-console
  }
};

const isBackground = () =>
{
  const isBackground = BackgroundGlobals.cacheStore !== null;
  return isBackground;
};

const getLogLevel = async () =>
{
  // Check if the current code is being ran in the background or foreground
  const context = isBackground() ? BackgroundGlobals : ForegroundGlobals;

  // get loglevel from the current context
  let level = context.loglevel;

  // if current context loglevel is null (aka not set, we will retrieve the level from storage)
  if (context.loglevel === null)
  {
    level = await getStorageLogLevelAsync();
    context.loglevel = (level === null || level === undefined) ? BuildSettings.LOG_LEVEL : level;
  }

  if (Number.isInteger(level) && Object.values(LogLevels).includes(level))
  {
    return level;
  }

  return BuildSettings.LOG_LEVEL;
};

const getStorageLogLevelAsync = async () =>
{
  const storageLocalGetAsync = new Promise((resolve) => chrome.storage.local.get(LocalStorageVariables.EXTENSION_LOGGING.name, resolve));
  const response = await storageLocalGetAsync;
  const storageLogLevel = response[LocalStorageVariables.EXTENSION_LOGGING.name];
  if (Number.isInteger(storageLogLevel) && Object.values(LogLevels).includes(storageLogLevel))
  {
    return storageLogLevel;
  }
  return null;
};
