import HttpService from '@extension/common/http_service';
import { extension } from '@extension/common/webextension';
import UIContentUtils from '@extension/utilities/ui_content_utils';
/////////////////////////////////
////////
//////////////////////////////////////////////////////////
//////////

export default class InteractiveBalloon
{
  constructor(waImageSecret)
  {
    this.root = null;
    this.defaultMcAfeeIcon = `/images/web_advisor/white_mcafee_icon.svg?secret=${waImageSecret}`;
  }

  async init(shadowRoot)
  {
    this.root = shadowRoot;

    const httpService = new HttpService();
    const responseStyles = await httpService.sendHttpRequest({ url: extension.runtime.getURL('css/interactive_balloon.css') });
    const stylesText = await responseStyles.text();
    let styleTag = `<style>${stylesText}</style>`;
/////////////////////////////////////
//////////////////////////////////////
//////////////
    this.root.innerHTML += styleTag;
  }

  create(options = {})
  {
    const { defaultIcon = this.defaultMcAfeeIcon, animateIcon = null } = options;
    const divContainer = document.createElement('div');
    divContainer.classList.add('mc-interactive-balloon');
    let imgTag = `<img src='${extension.runtime.getURL(animateIcon || defaultIcon)}' />`;
/////////////////////////////////////
//////////////////////////////////
//////////////
    divContainer.innerHTML += imgTag;
    this.root.append(divContainer);

    if (animateIcon)
    {
      setTimeout(() =>
      {
        const imageTag = divContainer.querySelector('img');
        UIContentUtils.fadeOut(imageTag, () =>
        {
          imageTag.setAttribute('src', extension.runtime.getURL(defaultIcon));
          UIContentUtils.fadeIn(imageTag);
        });
      }, 2000);
    }
  }
}
