/**
 * The different webpage/url states that exists within WebAdvisor
 */
export const BlockState = {
  GREEN: 0,
  YELLOW: 1,
  RED: 2,
  UNKNOWN: 3,
  HACKERSAFE: 4, // deprecated.
  TYPOSQUATTING: 5,
  PHISHING: 6,
  * [Symbol.iterator]() // Create an iterable that returns all the "bad" states
  {
    for (const prop of Object.keys(this))
    {
      if (prop !== 'GREEN' && prop !== 'UNKNOWN')
      {
        yield this[prop];
      }
    }
  }
};

