/**
 * Constructs a new object with the object key and values swapped.
 * @param   {Object} original - The original object to swap the key and values
 * @returns {Object} - The new constructed object with the key and values swapped
 */
export const swapObjectPropertyAndValues = (original) =>
{
  const swapped = {};

  Object.keys(original).forEach((prop) =>
  {
    swapped[original[prop]] = prop;
  });

  return swapped;
};

