import HttpService from '@extension/common/http_service';
import { extension } from '@extension/common/webextension';

export default class ResourceRequestor
{
  constructor()
  {
    this.http = new HttpService();
  }

  geti18nRes(attr, placeholders = [])
  {
    let str = extension.i18n.getMessage(attr, placeholders);
    const productName = extension.i18n.getMessage('res_PRODUCT_NAME_TRADEMARKED');
    str = str.replace(/%PRODUCTNAME%/g, productName);
    return str;
  }

  /**
   * Get the message based on the resource id
   * @param {Number}   resourceId - Resource id
   * @param {Function} callback   - Handles the returned localized string
   */
  getResStr(resourceId, callback)
  {
    const message = this.geti18nRes(resourceId);
    callback(
      message === '??' ? '' : message
    );
  }
}
