export const SocialMediaTypes = {
  NONE: 0,
  LINKEDIN: 1,
  INSTAGRAM: 2,
  YOUTUBE: 4,
  FACEBOOK: 8,
  TWITTER: 16,
  REDDIT: 32
};

// Sum of all Social Media Types
export const SocialMediaAllTypes = Object.values(SocialMediaTypes).reduce((sum, value) => sum + value);

export const SocialMediaActions = {
  TOGGLE_ON: 'TOGGLE_ON',
  TOGGLE_OFF: 'TOGGLE_OFF',
  ANNOTATION_ENABLE_ALL: 'ANNOTATION_ENABLE_ALL',
  ANNOTATION_DISABLE_ALL: 'ANNOTATION_DISABLE_ALL',
  ANNOTATION_ENABLE: 'ANNOTATION_ENABLE',
  ANNOTATION_DISABLE: 'ANNOTATION_DISABLE',
  GET: 'GET',
};
