import BackgroundIPC from '@extension/common/background_ipc';
import ResourceRequestor from '../../coreengine/resource_requestor';

export default class UIStrings
{
  constructor(iframeId = null, pageDocument = document)
  {
    this.resourcerequestor = new ResourceRequestor();
    this.document = pageDocument;

    if (iframeId)
    {
      const iframeContainer = this.document.querySelectorAll(`#${iframeId}`);
      if (iframeContainer) this.document = iframeContainer[0].contentDocument;
    }
  }

  /**
   * Passes message name into helper class to retrieve the appropriate localized message for it
   * @param   {string} attr - Name of the message to resolve as specified in the messages.json file
   * @returns {string} - The localized message for current locale
   */
  localeData(attr, placeholders = [])
  {
    return this.resourcerequestor.geti18nRes(attr, placeholders);
  }

  /**
   * Resolve a specified html element (through id) with the provided text
   * @param {string} id   - The id of the html element
   * @param {string} text - The text to be injected into the html element
   */
  fillText(id, text)
  {
    const element = this.document.getElementById(id);

    if (element)
    {
      const position = element.getAttribute('x-mcinsertposition');
      if (position)
      {
        element.insertAdjacentText(position, text);
        return;
      }

      element.textContent = text;
    }
  }

  /**
   * Resolves all html elements that need to be populated with localized string resources.
   * @returns {boolean} - Status provided showing if the function call was successful or not
   */
  fillLocalizedTexts()
  {
    for (const { id } of this.document.querySelectorAll('*[x-mclocalizedtext]'))
    {
      const text = this.localeData(id);

      if (text === null || text === undefined) return false;

      this.fillText(id, text);
    }

    return true;
  }

  /**
   * Fills all input place holder strings. These are hint string to be added to input html elements.
   * @returns {boolean} - Status provided showing if the function call was successful or not
   */
  fillInputPlaceHolders()
  {
    for (const { id } of this.document.querySelectorAll('*[x-inputplaceholder]'))
    {
      const text = this.localeData(id);
      const element = this.document.getElementById(id);

      if (text === null || text === undefined) return false;

      if (element !== null && element !== undefined)
      {
        element.setAttribute('placeholder', text);
      }
    }

    return true;
  }

  /**
   * Sends command to background to fill in placeholder text.
   * @param {string} id - Resource id
   */
  fillPlaceHolderInternal(id)
  {
    BackgroundIPC.getPlaceholderText(id).then((data) =>
    {
      this.fillText(id, data.text);
    }).catch(() => {});
  }

  /**
   * Fills all internal placeholders.
   * @returns {boolean} - Status provided showing if the function call was successful or not
   */
  fillPlaceHolders()
  {
    for (const { id } of this.document.querySelectorAll('*[x-mcplaceholder]'))
    {
      this.fillPlaceHolderInternal(id);
    }

    return true;
  }

  /**
   * Initializing steps for the class.
   */
  init()
  {
    // if failed to replace a text in locale then show blank page.
    this.fillLocalizedTexts();
    this.fillPlaceHolders();
    this.fillInputPlaceHolders();
  }
}
