// Common
import { extension } from '@extension/common/webextension';
import { Logger } from '@extension/common/logger';
import BackgroundIPC from '@extension/common/background_ipc';
// Telemetry
import { EventNames } from '@extension/telemetry/common_telemetry';
import ContextTelemetry from '@extension/telemetry/context_telemetry';
// Util
import { CommonUtils } from '@mcafee/form-detection/utils';
import UIStrings from '@extension/utilities/ui_strings';
import Utils from '@extension/utilities/utils';
import UIContentUtils from '@extension/utilities/ui_content_utils';

const SCREEN_TYPE = {
  SCAN_EMAIL: 'SCAN_EMAIL',
};

export default class DWS
{
  constructor(componentData, containerHandler)
  {
    this.componentData = componentData;

    // UI data
    this.email = componentData.email;
    this.dwsInfo = componentData.dwsInfo;

    // containerHandler related
    this.shadowRoot = containerHandler.shadowRoot;
    this.showContainer = containerHandler.showContainer;
    this.closeContainer = containerHandler.closeContainer;
    this.clickListener = containerHandler.clickListener;
    this.query = containerHandler.query;
    this.querySelector = containerHandler.querySelector;
    this.querySelectorAll = containerHandler.querySelectorAll;
    this.interactiveBalloon = containerHandler.interactiveBalloon;

    // Telemetry
    this.telemetryInfo = {
      uniqueEmailCount: componentData.uniqueEmailCount,
      screen: null,
      flowId: null
    };
  }

  async init()
  {
    const screenData = this.getScreenType();
    if (!screenData) return;

    // Create container messages
    this.createContentContainer(screenData);

    // Resolve locales
    this.initLocales();

    // Attach click events to different component pieces
    this.initListeners(screenData);

    // Add custom typefaces and inject into the main document because shadow DOM does not load custom fonts.
    // For example Google Fonts must be added to the main document (light DOM) for typeface to work.
    try
    {
      // Poppins
      const poppinsUrl = extension.runtime.getURL('fonts/Poppins-Regular.ttf');
      const poppinsFont = new FontFace('mcafeePoppins', `url(${poppinsUrl})`);
      await poppinsFont.load(); // wait for font to be loaded
      document.fonts.add(poppinsFont); // add font to document

      // OpenSans
      const openSansUrl = extension.runtime.getURL('fonts/OpenSans-Regular.ttf');
      const openSansFont = new FontFace('mcafeeOpenSans', `url(${openSansUrl})`);
      await openSansFont.load(); // wait for font to be loaded
      document.fonts.add(openSansFont); // add font to document
    }
    catch (error)
    {
      Logger.error(error);
      return;
    }

    // Show balloon message after everything has loaded
    this.showContainer();
  }

  /**
   * If email was scanned before, we need to show the breach detail screen directly. Other wise we need to
   * show the initial screen to offer a DWS as long as it has been at least 24h since last time.
   */
  getScreenType()
  {
    if (!this.dwsInfo) return null;

    const { shownCount } = this.dwsInfo;
    return {
      type: SCREEN_TYPE.SCAN_EMAIL,
      shownCount: shownCount || 0
    };
  }

  /**
   * When called, it will hide all active sections and load the screen passed.
   * Pass handlers if you want custom behavior during the load process.
   * This method switches between the different DWS screens
   */
  loadScreen(screenSelector, cbHandlers = {})
  {
    const { onFadeOut, onScreenActive } = cbHandlers;
    const screen = this.querySelector('.screens-container');

    UIContentUtils.fadeOut(screen, () =>
    {
      onFadeOut && onFadeOut();

      // Either first time rendering (meaning no active screen), or component has been already rendered before
      const currActiveScreenEl = this.querySelector('.screen.active');
      if (currActiveScreenEl)
      {
        currActiveScreenEl.classList.remove('active');
      }

      this.querySelector(screenSelector).classList.add('active');

      onScreenActive && onScreenActive();

      UIContentUtils.fadeIn(screen);
    });
  }

  /**
   * Launch IDPS login flow
   */
  scan()
  {
    const emailInput = this.querySelector('#email-input-wrapper > .wa-text-input > input');
    const emailLabel = this.querySelector('#email-input-wrapper > .wa-text-input > label');

    const isValidEmail = this.validateEmail(emailInput, emailLabel);
    if (!isValidEmail) return;

    BackgroundIPC.updateDWSWhitelist(this.email);
    this.closeContainer();
    BackgroundIPC.launchIDPSLogin();
  }

  /**
   * Initialize locales
   */
  initLocales()
  {
    this.strConvertor = new UIStrings(null, this.shadowRoot);
    this.strConvertor.init();
  }

  /**
   * Initialize event listeners for all screens.
   */
  initListeners()
  {
    // Handles the different ways of closing the balloon component
    // Map of id names with their corresponding telemetry action
    const closeButtonIds = {
      '#dws_thanks_btn': {
        eventAction: 'Acknowledged',
      },
      '#dws_next_time_btn': {
        eventAction: 'Declined',
      },
      '#dws_do_not_ask_again_close_btn': {
        eventAction: 'Acknowledged',
      },
      '#close-icon-btn': {
        eventAction: 'XClicked',
      }
    };
    const closeButtons = this.querySelectorAll(Object.keys(closeButtonIds).join(', '));

    for (const button of Array.from(closeButtons))
    {
      button.addEventListener('click', (event) =>
      {
        let { eventAction } = closeButtonIds[`#${event.target.id}`];

        if (this.dwsInfo.shownCount >= 2 && eventAction === 'XClicked')
        {
          this.telemetryInfo.screen = 'wont_ask_again';
          eventAction = 'Impression';
        }

        ContextTelemetry.handle({
          flowId: this.telemetryInfo.flowId,
          identifier: EventNames.DARK_WEB_SCAN,
          count: this.telemetryInfo.uniqueEmailCount,
          eventAction,
          type: this.telemetryInfo.screen
        });

        this.closeContainer();
      });
    }

    // Scan email buttons handler
    const scanEmailButtonsId = [
      '#dws_scan_btn',
      '#dws_scan_btn_first_time'
    ];
    const scanEmailButtons = this.querySelectorAll(scanEmailButtonsId.join(', '));

    for (const button of Array.from(scanEmailButtons))
    {
      // eslint-disable-next-line no-loop-func
      button.addEventListener('click', Utils.debounce(() =>
      {
        ContextTelemetry.handle({
          flowId: this.telemetryInfo.flowId,
          identifier: EventNames.DARK_WEB_SCAN,
          count: this.telemetryInfo.uniqueEmailCount,
          eventAction: 'Accepted',
          type: this.telemetryInfo.screen
        });

        this.scan();
      }), 1000);
    }

    const remindLaterButton = this.querySelector('#dws_remind_later_btn');

    remindLaterButton.addEventListener('click', () =>
    {
      // Show remind me later UI
      this.loadScreen('#screen-remind-later', {
        onScreenActive: () =>
        {
          this.telemetryInfo.screen = 'ask_again_next_time';
          ContextTelemetry.handle({
            flowId: this.telemetryInfo.flowId,
            identifier: EventNames.DARK_WEB_SCAN,
            count: this.telemetryInfo.uniqueEmailCount,
            eventAction: 'Impression',
            type: this.telemetryInfo.screen
          });
        }
      });
    });

    const dontAskAgainBtn = this.querySelector('#dws_do_not_ask_again_btn');
    dontAskAgainBtn.addEventListener('click', () =>
    {
      // Add email to whitelist to be excluded from offering DWS in the future
      BackgroundIPC.updateDWSWhitelist(this.email);

      // Clean up cache for this email
      BackgroundIPC.clearCachedDWSInfo(this.email);

      // Show don't ask again UI
      this.loadScreen('#screen-do-not-ask-again', {
        onScreenActive: () =>
        {
          this.telemetryInfo.screen = 'wont_ask_again';
          ContextTelemetry.handle({
            flowId: this.telemetryInfo.flowId,
            identifier: EventNames.DARK_WEB_SCAN,
            count: this.telemetryInfo.uniqueEmailCount,
            eventAction: 'Impression',
            type: this.telemetryInfo.screen
          });
        }
      });
    });

    // These contain the email inputs on the scan email screen and the sign in account screen
    const emailInput = this.querySelector('.wa-text-input > #email-input');
    const emailLabel = this.querySelector('.wa-text-input > label');

    emailInput.addEventListener('blur', () =>
    {
      emailInput.style.border = '1px solid #ABB2C3';
      emailLabel.style.color = '#5A6175';
      this.validateEmail(emailInput, emailLabel);
    });

    emailInput.addEventListener('focus', (event) =>
    {
      event.currentTarget.select();
      emailInput.style.border = '1px solid #4258FF';
      emailLabel.style.color = '#4258FF';
    });
  }

  validateEmail(emailInput, emailLabel)
  {
    const errorPrompt = this.querySelector('.email-error-div');
    const errorX = this.querySelector('.email-error-x');

    if (!CommonUtils.validateEmailFormat(emailInput.value))
    {
      emailInput.classList.add('wa-text-input-not-email');
      emailLabel.classList.add('wa-text-label-not-email');
      errorPrompt.style.display = 'block';
      errorX.style.display = 'block';
      Logger.log('The email entered is not in a valid email format');
      return false;
    }

    emailInput.classList.remove('wa-text-input-not-email');
    emailLabel.classList.remove('wa-text-label-not-email');
    errorPrompt.style.display = 'none';
    errorX.style.display = 'none';
    return true;
  }

  /**
   * Call to load the initial screen.
   */
  createContentContainer(screenData)
  {
    const mainContainer = this.querySelector('.mc-dws-main-container');

    switch (screenData.type)
    {
      case SCREEN_TYPE.SCAN_EMAIL:
      {
        const screenScanEmail = this.querySelector('#screen-scan-email');
        screenScanEmail.classList.add('active');

        const emailInput = this.querySelector('.wa-text-input > #email-input');
        emailInput.setAttribute('value', this.email);

        // Show correct actions based on amount of times UI has been shown before
        if (screenData.shownCount > 0)
        {
          // Second and Third appearance telemetry
          this.telemetryInfo.flowId = 'add-email-to-monitoring-subsequent-time-shown';
          this.telemetryInfo.screen = 'identity_toast_3_buttons';
        }
        else
        {
          // First appearance telemetry
          this.telemetryInfo.flowId = 'add-email-to-monitoring-first-time-shown';
          this.telemetryInfo.screen = 'identity_toast_2_buttons';
        }

        // show the 'Remind me next time' on appearances first 2 appearances, 'Don't ask again' on third appearance
        if (screenData.shownCount > 1)
        {
          this.querySelector('.actions.actions-first-time').style.display = 'none';
        }
        else
        {
          this.querySelector('.actions.actions-second-time').style.display = 'none';
        }

        mainContainer.style.display = 'block';

        BackgroundIPC.updateDWSShown(this.email);
        break;
      }
    }

    // Send impression telemetry
    ContextTelemetry.handle({
      flowId: this.telemetryInfo.flowId,
      identifier: EventNames.DARK_WEB_SCAN,
      count: this.telemetryInfo.uniqueEmailCount,
      eventAction: 'Impression',
      type: this.telemetryInfo.screen
    });
  }
}
