import { extension } from '@extension/common/webextension';
import UIStrings from '@extension/utilities/ui_strings';
import { EventNames } from '@extension/telemetry/common_telemetry';
import ContextTelemetry from '@extension/telemetry/context_telemetry';
import UIContentUtils from '@extension/utilities/ui_content_utils';
import { Logger } from '@extension/common/logger';
import { Globals } from '@core-engine/globals';

const BALLOON_STATE = {
  COLLAPSE: 'COLLAPSE',
  EXPAND: 'EXPAND',
  CLOSE: 'CLOSE',
};

export const BALLOON_TRIGGER_TYPE = {
  FINANCIAL: 'FINANCIAL',
  EMAIL: 'EMAIL',
  ECOMMERCE: 'ECOMMERCE',
  SIGNUP: 'SIGNUP',
};

export default class AdvancedProtectionSignal
{
  constructor(data, container)
  {
    this.data = data;
    this.container = container; // ShadowDomContainer
    this.strConvertor = new UIStrings(null, this.container.shadowRoot);

    // Details
    this.triggerType = data.triggerType && data.triggerType.toLowerCase();
    this.color = data.rating && data.rating.toLowerCase();
    this.sequence = 2;

    // Common Selectors
    this.shield = this.container.querySelector('.shield');
    this.shieldImage = this.container.querySelector('.shield > img');
    this.signalExpanded = this.container.querySelector('.signal--expanded');

    // Shield Animation
    this.autoExpandedByTimer = false;
    this.autoCollapseTimer = null;
    this.shieldAnimation = null;
    this.onFinishAnimation = this.onFinishAnimation.bind(this);

    // telemetry
    this.telemetryBalloonSent = false;
    this.telemetryHoverSent = false;

    // balloon text
    this.balloonText = null;
  }

  async init()
  {
    if (!this.triggerType || !this.color)
    {
      Logger.warn('APS Balloon init: triggerType/color not provided');
      return;
    }

    // Initialize Font on document
    await UIContentUtils.initFontOnDocument();

    const configureStyles = (color, imageSrc, locale) =>
    {
      const { waImageSecret } = this.data;
      this.shield.style.backgroundColor = color;
      this.signalExpanded.style.backgroundColor = color;

      this.container.querySelector('.rating-image').src = extension.runtime.getURL(`${imageSrc}?secret=${waImageSecret}`);
      this.container.querySelector('#expanded-text').textContent = this.strConvertor.localeData(locale);
      this.balloonText = this.strConvertor.localeData(locale);
    };

    switch (this.data.rating)
    {
      case 'GREEN':
        {
          const GREEN = '#0a7155';
          configureStyles(GREEN, 'images/advanced_protection_signals/checkmark_aps_signal.svg', `aps_expanded_shield_site_${this.triggerType.toLowerCase()}`);
        }
        break;
      case 'GREY':
        {
          const GREY = '#848FAA';
          configureStyles(GREY, 'images/advanced_protection_signals/question-mark.svg', 'aps_expanded_shield_site_unknown');
        }
        break;
      default:
        throw new Error('Site rating was not provided...');
    }

    let height = '96px';
    let width = '378px';
    if (this.balloonText && this.balloonText.length > 86)
    {
      height = '120px';
      width = '430px';
      this.container.query('.signal--expanded').width('370px');
    }

    const animationInfo = {
      properties: [{ width, height }],
      timing: { fill: 'both', duration: 1000, easing: 'ease-in-out', }
    };

    // Create animation and add listeners for mouse events
    this.shieldAnimation = new Animation('shield', this.shield, animationInfo);
    this.shieldAnimation.createAnimation(this.onFinishAnimation);
    this.initAnimationListener();

    // Show component on the page
    this.container.showContainer();

    this.shieldAnimation.startTimeout(); // Initialize auto close timer

    // If we are to show the full message, we will set a timer to open the
    // full message after 1 seconds, then we will set a time to auto collapse
    // the BubbleSignal. Note that the "startTimeout" set above, will be stopped
    // through the expandBubbleSignal() > enter() function
    if (this.data.fullMessage)
    {
      this.sequence = 1;

      setTimeout(() =>
      {
        // Send telemetry full message impression
        this.options = {
          identifier: EventNames.APS.IMPRESSION_BALLOON,
          sequence: this.sequence,
          trigger: this.triggerType,
          color: this.color,
        };
        ContextTelemetry.handle(this.options);

        this.autoExpandedByTimer = true;
        // initialize timer (will stop when calling expandBubbleSignal)
        this.expandBubbleSignal();

        // Start 3 seconds countdown to close balloon
        this.autoCollapseTimer = setTimeout(() =>
        {
          this.collapseBubbleSignal(true);
        }, 5000);
      }, 1000);
    }
    else
    {
      // Send telemetry full message impression
      this.options = {
        identifier: EventNames.APS.IMPRESSION_BALLOON,
        sequence: this.sequence,
        trigger: this.triggerType,
        color: this.color,
      };
      ContextTelemetry.handle(this.options);
    }
  }

  onFinishAnimation(state)
  {
    if (state === BALLOON_STATE.EXPAND)
    {
      this.signalExpanded.style.opacity = 1;
    }

    if (state === BALLOON_STATE.CLOSE)
    {
      UIContentUtils.fadeOut(this.shield, () =>
      {
        this.container.closeContainer();
      });
    }
  }

  initAnimationListener()
  {
    this.shield.addEventListener('mouseenter', () =>
    {
      // Send Telemetry once for on Hover
      if (!this.telemetryHoverSent)
      {
        this.options = {
          identifier: EventNames.APS.IMPRESSION_HOVER,
          sequence: this.sequence,
          trigger: this.triggerType,
          color: this.color,
        };
        ContextTelemetry.handle(this.options);
        this.telemetryHoverSent = true;
      }

      // If the user clicked on the X to close, we can ignore any other request and close the UI
      if (this.shieldAnimation.isClosing())
      {
        return;
      }

      // Ignore expanding balloon since it already expanded by the auto timer
      if (this.autoExpandedByTimer)
      {
        this.autoExpandedByTimer = false; // reset so user can leave and enter the shield
        if (this.autoCollapseTimer !== null)
        {
          // Since user enter the balloon we can stop the auto close balloon timer
          clearTimeout(this.autoCollapseTimer);
        }
        return;
      }

      this.expandBubbleSignal();
    });

    this.shield.addEventListener('mouseleave', () =>
    {
      if (this.shieldAnimation.isClosing())
      {
        return;
      }

      this.collapseBubbleSignal(false);
    });

    // Add listener to remove the component (shown on expanded view)
    this.container.querySelector('#aps-close-icon').addEventListener('click', () =>
    {
      // Telemetry on close click
      this.options = {
        identifier: EventNames.APS.CLOSE_BALLOON,
        sequence: this.sequence,
        trigger: this.triggerType,
        color: this.color,
      };
      ContextTelemetry.handle(this.options);

      this.collapseBubbleSignal(true);
    });
  }

  // common on collapse bubble function
  collapseBubbleSignal(close)
  {
    this.shieldAnimation.pause();
    this.signalExpanded.style.opacity = 0;
    setTimeout(() =>
    {
      this.shieldImage.style.opacity = 1;
      this.shieldAnimation.leave(close);
    }, 200);
  }

  expandBubbleSignal()
  {
    this.shieldAnimation.pause();
    this.shieldImage.style.opacity = 0;
    this.shieldAnimation.enter();
  }
}

// HELPER CLASS : Create bubble Animation
class Animation
{
  constructor(name, element, options)
  {
    this.options = options;
    this.element = element;
    this.name = name;
    this.state = BALLOON_STATE.COLLAPSE;

    // timer for auto close
    this.timer = null;
    this.onAnimationFinish = null;
  }

  createAnimation(onAnimationFinish = null)
  {
    if (onAnimationFinish)
    {
      this.onAnimationFinish = onAnimationFinish;
    }

    this.animation = this.element.animate(this.options.properties, this.options.timing);
    this.animation.pause();
    this.animation.onfinish = () =>
    {
      onAnimationFinish && onAnimationFinish(this.state);
    };
  }

  pause()
  {
    if (this.animation.playState === 'running') this.animation.pause();
  }

  isClosing()
  {
    return this.state === BALLOON_STATE.CLOSE;
  }

  enter()
  {
    this.stopTimeout();
    this.state = BALLOON_STATE.EXPAND;
    this.runForwards();
  }

  leave(close)
  {
    if (!close && this.timer !== null)
    {
      this.startTimeout();
    }

    this.state = close ? BALLOON_STATE.CLOSE : BALLOON_STATE.COLLAPSE;
    this.runReverse();
  }

  runReverse()
  {
    this.animation.playbackRate = -1;
    this.animation.play();
  }

  runForwards()
  {
    this.animation.playbackRate = 1;
    this.animation.play();
  }

  stopTimeout()
  {
    if (this.timer !== null)
    {
      clearTimeout(this.timer);
    }
  }

  startTimeout()
  {
    if (this.timer !== null)
    {
      clearTimeout(this.timer);
    }

    this.timer = setTimeout(() =>
    {
      this.onAnimationFinish(BALLOON_STATE.CLOSE);
    }, Globals.bkGlobals.apsAnimationTimeout);
  }
}
