import { BackgroundGlobals } from '@extension/background/background_globals';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';

/**
 * Tab data class and it's helper functions
 */
export default class TabData
{
  // Values that will get reset everytime we nativagate to a new page
  static getResetValues()
  {
    return { blockedIframes: [], cryptoBlockData: null, tabDomainTrusted: false };
  }

  static getDefaultValues()
  {
    return TabData.getResetValues();
  }

  reset(tabId)
  {
    this.update(tabId, TabData.getResetValues());
  }

  get(tabId)
  {
    const cache = BackgroundGlobals.cacheStore.get(LocalStorageVariables.CACHE_TAB_DATA.name, {});
    return cache[tabId] || TabData.getDefaultValues();
  }

  set(tabId, newTabData)
  {
    const cache = BackgroundGlobals.cacheStore.get(LocalStorageVariables.CACHE_TAB_DATA.name, {});
    cache[tabId] = newTabData;
    BackgroundGlobals.cacheStore.set(LocalStorageVariables.CACHE_TAB_DATA.name, cache);
  }

  delete(tabId)
  {
    const cache = BackgroundGlobals.cacheStore.get(LocalStorageVariables.CACHE_TAB_DATA.name, {});
    delete (cache[tabId]);
  }

  update(tabId, newTabData)
  {
    const currentTabData = this.get(tabId);
    this.set(tabId, { ...currentTabData, ...newTabData });
  }

  setTabBlockPageData(tabId, blockPageData)
  {
    const currentTabData = this.get(tabId);
    this.set(tabId, { ...currentTabData, blockPageData });
  }

  addBlockedFrame(tabId, value)
  {
    const currentTabData = this.get(tabId);
    currentTabData.blockedIframes.push(value);
  }

  getBlockedFrames(tabId)
  {
    const currentTabData = this.get(tabId);
    return currentTabData.blockedIframes;
  }

  resetBlockedFrames(tabId)
  {
    const currentTabData = this.get(tabId);
    currentTabData.blockedIframes = [];
  }
}
