// Background
import { BackgroundGlobals } from '@extension/background/background_globals';
// Common
import { BlockState, BrowserType, BuildSettings, Color, Score } from '@extension/common/constants';
import { BackgroundConstants } from '@extension/common/background_constants';
import { extension } from '@extension/common/webextension';
import { Logger } from '@extension/common/logger';
import { LocalStorageVariables } from '@extension/common/cache_store_variables';
// @mcafee
import ResourceManager from '@mcafee/wa-annotations/Common/resource_manager';

/**
 * TODO: Comments
 */
export default class BackgroundCommon
{
  static async toGTICategoriesString(categories, includeDescription = false)
  {
    const getRes = (l10nId) => new Promise((resolve) =>
    {
      ResourceManager.getResString(l10nId, (res) => resolve(res));
    });

    const getCategoryStrings = async (categoryId, includeDescription) =>
    {
      const newCategory = {};

      const name = await getRes(`category_id_${categoryId}_name`);
      newCategory.name = name;

      if (includeDescription)
      {
        const description = await getRes(`category_id_${categoryId}_description`);
        newCategory.description = description;
      }

      return newCategory;
    };

    const promises = [];
    for (const category of categories)
    {
      promises.push(getCategoryStrings(category, includeDescription));
    }

    const newCategories = await Promise.all(promises);
    return newCategories;
  }

  /**
   * Determine the shorthand browser type name based on its corresponding index value.
   * @returns {String} - The browser name (shorthand)
   */
  static browserTypeToString(toLowerCase = false)
  {
    let browserType = '';
    switch (BuildSettings.BROWSER_TYPE)
    {
      case 1:
        browserType = 'FF';
        break;
      case 2:
        browserType = 'CH';
        break;
      case 3:
        browserType = 'ED';
        break;
      default:
        browserType = 'UN';
    }

    return toLowerCase ? browserType.toLowerCase() : browserType;
  }

  /**
   * TODO: Comments
   */
  static toImagePaths(imageName)
  {
    let imagePath = 'images/browser_action/';

    const legacyImageName = imageName.startsWith('small_') ? imageName : `small_${imageName}`;

    switch (legacyImageName)
    {
      case 'small_button_green.gif':
        imagePath += 'green';
        break;
      case 'small_button_yellow.gif':
        imagePath += 'yellow';
        break;
      case 'small_button_red.gif':
        imagePath += 'red';
        break;
      case 'small_button_grey.gif':
        imagePath += 'white';
        break;
      case 'small_button_disabled.gif':
        imagePath += 'disabled';
        break;
      default:
        imagePath += imageName;
        break;
    }

    // 16 and 32 used by Chrome/Firefox, 20 and 40 used by Edge
    const imagePaths = {
      size16: `${imagePath}_16.png`,
      size20: `${imagePath}_20.png`,
      size32: `${imagePath}_32.png`,
      size40: `${imagePath}_40.png`
    };

    return imagePaths;
  }

  /**
   * TODO: Comments
   */
  static colorToImagePaths(color)
  {
    let imageName = 'white';

    switch (color)
    {
      case Color.GREEN:
        imageName = 'green';
        break;
      case Color.YELLOW:
        imageName = 'yellow';
        break;
      case Color.RED:
        imageName = 'red';
        break;
      case Color.UNKNOWN:
        imageName = 'white';
        break;
      case Color.DISABLED:
        imageName = 'disabled';
        break;
    }

    return BackgroundCommon.toImagePaths(imageName);
  }

  /**
   * Return the appropriate class for the annotation item
   * @param {Object} annotationItem - Reputation object returned by the GTI server
   */
  static toAnnotationImageClass(annotationItem)
  {
    const color = BackgroundCommon.toColor(annotationItem);

    let imageClass = '';

    switch (color)
    {
      case Color.GREEN:
        imageClass = 'OK';
        break;
      case Color.RED:
        imageClass = 'WARN';
        break;
      case Color.YELLOW:
        imageClass = 'INFO';
        break;
      case Color.UNKNOWN:
        imageClass = 'UNKNOWN';
        break;
    }

    return imageClass;
  }

  /**
  * Determines if the URL provided is malicious or not.
  * @param   {Object} categories - Url info deemed phishing / malicious
  * @returns {boolean} - Outcome of the phishing site check
  */
  static isPhishingURI(categories)
  {
    return categories && categories.includes(BackgroundConstants.PHISHING_CATEGORY);
  }

  /**
   * TODO: Comments
   */
  static toBlockState(annItem)
  {
    const colorVal = BackgroundCommon.toColor(annItem);

    if (colorVal === Color.RED || colorVal === Color.YELLOW)
    {
      if (BackgroundCommon.isPhishingURI(annItem.category))
      {
        return BlockState.PHISHING;
      }
    }

    // Because the color and block state values correspond to the numbers, you can simply return the color value
    return isNaN(colorVal) ? BlockState.UNKNOWN : colorVal;
  }

  /**
   * TODO: Comments
   */
  static toColor(annItem)
  {
    let color = Color.UNKNOWN;

    if (annItem === undefined || annItem.trust === undefined)
    {
      return color;
    }

    if (annItem.trust >= Score.CLEAN)
    {
      color = Color.GREEN;
    }
    else if (annItem.trust > Score.UNVERIFIED)
    {
      color = Color.YELLOW;
    }
    else if (annItem.trust >= Score.DIRTY)
    {
      color = Color.RED;
    }
    else
    {
      color = Color.UNKNOWN;
    }

    return color;
  }

  /**
   * TODO: Comments
   */
  static toSiteReportUrl(url)
  {
    const siteReportUrl = extension.runtime.getURL('html/site_status_site_report.html');
    return `${siteReportUrl}?url=${escape(url)}`;
  }

  static isExtensionUrl(url)
  {
    // Chrome/Edge Internal Pages
    if ((BrowserType.CHROME === BuildSettings.BROWSER_TYPE && url.startsWith(`chrome-extension://${extension.runtime.id}`))
    || (BrowserType.EDGE === BuildSettings.BROWSER_TYPE && url.startsWith(`ms-browser-extension://${extension.runtime.id}`)))
    {
      return true;
    }

    // Firefox Internal Pages
    if (BrowserType.FIREFOX === BuildSettings.BROWSER_TYPE)
    {
      let firefoxInternalUUID = extension.i18n.getMessage('@@extension_id');
      if (!firefoxInternalUUID)
      {
        const internalPageUrl = extension.runtime.getURL('images/web_advisor/logo.png');
        // get internal uuid from resource url extracting characters between 'moz-extension://' and '/'
        // sample url: moz-extension://ABCDE/images/web_advisor/logo.png => ABCDE
        const urlSplit = internalPageUrl.split('/');
        firefoxInternalUUID = (urlSplit.length >= 3) ? urlSplit[2] : '';
      }

      return firefoxInternalUUID && url.startsWith(`moz-extension://${firefoxInternalUUID}`);
    }

    return false;
  }

  // Check whether a url is a siteadvisor url or our own extension url
  static isSafeUrl(url)
  {
    return BackgroundCommon.isSiteAdvisorUrl(url) || BackgroundCommon.isExtensionUrl(url);
  }

  static isSiteAdvisorUrl(url)
  {
    return (url.startsWith(`http://${BackgroundConstants.SITE_ADVISOR_URL}`)
    || url.startsWith(`https://${BackgroundConstants.SITE_ADVISOR_URL}`));
  }

  static isPropertRedirectUrl(url)
  {
    return BackgroundCommon.isPropertyWebURL(url) || url.startsWith('edge://') || url.startsWith('chrome://');
  }

  // Check whether a url is a proper web url
  static isProperWebURL(url)
  {
    if (url.startsWith('http://') || url.startsWith('https://') || url.startsWith('ftp://'))
    {
      return true;
    }

    return false;
  }

  static getUINumber(number)
  {
    // sample output:
    // 0: 0
    // 999: 999
    // 1010: 1k+
    // 1251: 1.2k+
    // 9999: 9.9k+
    // > 10000: 10k+
    const THOUSAND = 1000;
    const TEN_THOUSAND = 10000;
    if (number >= TEN_THOUSAND) return '10k+';
    if (number >= THOUSAND)
    {
      let decimal = (number / THOUSAND).toFixed(1);
      decimal = decimal.substring(2, 3) === '0' ? decimal.substring(0, 1) : decimal;
      return `${decimal}k+`;
    }
    return `${number}`;
  }

  static hasEpochTimeElapsed(initialEpochInSeconds, timePassedinSeconds)
  {
    const nowInSeconds = (new Date()).getTime() / 1000;
    if (nowInSeconds > initialEpochInSeconds + timePassedinSeconds) return true;
    return false;
  }

  static isPersonalPolicyEnabled()
  {
/////////////////////////////////////
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////////////////////
/////
///////////////////
/////
//////////////

    return true;
  }

  static isActivityPolicyEnabled()
  {
/////////////////////////////////////
/////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////////////////////////////////////////////////////
/////
///////////////////
/////
//////////////

    return true;
  }

  static blockRequest(tabId, url, message)
  {
    extension.declarativeNetRequest.getDynamicRules(
      (rules) =>
      {
        const found = rules.filter((rule) => rule.condition.urlFilter === url);

        if (found.length === 0)
        {
          extension.declarativeNetRequest.updateDynamicRules(
            {
              addRules: [{
                id: rules.length + 1,
                condition: {
                  domains: ['<all_urls>'],
                  urlFilter: url,
                  resourceTypes: ['main_frame']
                },
                action: {
                  type: 'block'
                }
              }]
            },
            () =>
            {
              Logger.log(`${message} ${url} ${tabId}`);
            }
          );
        }
      }
    );
  }
}
