import UIStrings from '@extension/utilities/ui_strings';
// Telemetry
import { EventNames } from '@extension/telemetry/common_telemetry';
import ContextTelemetry from '@extension/telemetry/context_telemetry';
import UIContentUtils from '@extension/utilities/ui_content_utils';

export default class AdvancedProtectionSignalToast
{
  constructor(data, container)
  {
    this.data = data;
    this.container = container; // ShadowDomContainer
    this.strConvertor = new UIStrings(null, this.container.shadowRoot);
    this.strConvertor.init();
  }

  async init()
  {
    // Initialize Font on document
    await UIContentUtils.initFontOnDocument();

    // Show component on the page
    this.initListener();
    this.container.showContainer();
    this.container.query('.toast__text').focus();

    this.options = {
      identifier: EventNames.APS.EVENT_NAME,
    };
    ContextTelemetry.handle(this.options);
  }

  initListener()
  {
    this.container.clickListener('#aps_toast_button', () =>
    {
      // Telemetry for Got it
      this.options = {
        identifier: EventNames.APS.TOAST_CLOSE,
        gotItButton: true,
      };
      ContextTelemetry.handle(this.options);

      this.container.closeContainer();
    });

    this.container.clickListener('.container__signal', () =>
    {
      // Telemetry for click on Shield
      this.options = {
        identifier: EventNames.APS.TOAST_CLOSE,
        gotItButton: false,
      };
      ContextTelemetry.handle(this.options);

      this.container.closeContainer();
    });
  }
}
