import { Globals } from './globals';

export default class ResourceManager
{
  /**
   * Obtain the corresponding string for the resource name
   * @param {String}   name     - The name of the resource
   * @param {Function} callback - Handles the localized string returned
   */
  static getResString(name, callback)
  {
    Globals.ANNOTATION.resourceRequestor.getResStr(name, (value) =>
    {
      if (!value)
      {
        Globals.Logger.error(`Resource string ${name} failed to fetch through resource requestor`);
      }
      else
      {
        callback(value);
      }
    });
  }
}
