/**
 * Wrapper class making the http request call (using the built-in Fetch api).
 * Handling normal and exception cases.
 */
export default class HttpService
{
  onHttpResponse(response)
  {
    if (!response.ok)
    {
      const message = `HttpService Status: ${response.status}\nError Text: ${response.statusText}`;
      return Promise.reject(new Error(message));
    }

    return response;
  }

  sendHttpRequest({ url, headers, body, method = 'GET' })
  {
    return fetch(url, { headers, method, body }).then(this.onHttpResponse);
  }
}
