export default class Utils
{
  /**
   * Creates a debounced function that delays invoking func until after wait
   * milliseconds have elapsed since the last time the debounced function was invoked.
   * source: http://davidwalsh.name/javascript-debounce-function
  */
  static debounce(func, wait, immediate)
  {
    let timeout;
    return (...args) =>
    {
      const later = () =>
      {
        timeout = null;
        if (!immediate) func(...args);
      };

      const callNow = immediate && !timeout;
      clearTimeout(timeout);

      timeout = setTimeout(later, wait);
      if (callNow) func(...args);
    };
  }

  static isBrowserEN()
  {
    const language = extension.i18n.getUILanguage();
    return language.toLowerCase().startsWith('en');
  }

  static isChromeBrowser()
  {
    // window.opr is only defined for opera. Edge Chromium has user agent Edg. So avoid these two as
    // chrome extension can be installed even in these two popular browsers, window.navigator
    const ua = navigator.userAgent.toLowerCase();

    // Check if browser is chromium based, window.chrome
    if (!ua.includes('chrome') || !!chrome === false) return false;

    // check if it is opera browser, window.opr
    if (ua.includes('opr')) return false;

    // check if it is edge browser
    if (ua.includes('edg')) return false;

    return true;
  }
}

/**
 * Vanilla JS solution to JQuery's $(document).ready() API
 */
export const onDOMContentReady = (cb) =>
{
  if (typeof document === 'undefined' || document === null)
  {
    return;
  }

  if (document.readyState === 'complete' || (document.readyState !== 'loading' && !document.documentElement.doScroll))
  {
    cb();
  }
  else
  {
    document.addEventListener('DOMContentLoaded', cb);
  }
};

export const getBrowserInfo = () =>
{
  const ua = navigator.userAgent.toLowerCase();

  const match = /(edge)\/([\w.]+)/.exec(ua)
        || /(edg)\/([\w.]+)/.exec(ua)
        || /(opr)[/]([\w.]+)/.exec(ua)
        || /(chrome)[ /]([\w.]+)/.exec(ua)
        || /(iemobile)[/]([\w.]+)/.exec(ua)
        || /(version)(applewebkit)[ /]([\w.]+).*(safari)[ /]([\w.]+)/.exec(ua)
        || /(webkit)[ /]([\w.]+).*(version)[ /]([\w.]+).*(safari)[ /]([\w.]+)/.exec(ua)
        || /(webkit)[ /]([\w.]+)/.exec(ua)
        || /(opera)(?:.*version|)[ /]([\w.]+)/.exec(ua)
        || /(msie) ([\w.]+)/.exec(ua)
        || (ua.indexOf('trident') >= 0 && /(rv)(?::| )([\w.]+)/.exec(ua))
        || (ua.indexOf('compatible') < 0 && /(mozilla)(?:.*? rv:([\w.]+)|)/.exec(ua))
        || [];

  const platformMatch = /(ipad)/.exec(ua)
        || /(ipod)/.exec(ua)
        || /(windows phone)/.exec(ua)
        || /(iphone)/.exec(ua)
        || /(kindle)/.exec(ua)
        || /(silk)/.exec(ua)
        || /(android)/.exec(ua)
        || /(win)/.exec(ua)
        || /(mac)/.exec(ua)
        || /(linux)/.exec(ua)
        || /(cros)/.exec(ua)
        || /(playbook)/.exec(ua)
        || /(bb)/.exec(ua)
        || /(blackberry)/.exec(ua)
        || [];

  const matched = {
    browser: match[5] || match[3] || match[1] || '',
    version: match[4] || match[2] || '0',
    platform: platformMatch[0] || '',
  };

  if (matched.browser === 'mozilla')
  {
    matched.browser = 'firefox';
  }

  if (matched.browser === 'edg')
  {
    matched.browser = 'edge';
  }

  return matched;
};

/**
 * Source: https://developer.mozilla.org/en-US/docs/Web/API/SubtleCrypto/digest
 */
export async function digestMessage(message)
{
  const msgUint8 = new TextEncoder().encode(message); // encode as (utf-8) Uint8Array
  const hashBuffer = await crypto.subtle.digest('SHA-256', msgUint8); // hash the message
  const hashArray = Array.from(new Uint8Array(hashBuffer)); // convert buffer to byte array
  const hashHex = hashArray.map((b) => b.toString(16).padStart(2, '0')).join(''); // convert bytes to hex string
  return hashHex;
}
