import { extension } from '@extension/common/webextension';
import { BackgroundCommands, ContentCommands } from '@extension/common/commands';
import HandlePromise from '@extension/common/handle_promise';

/**
 * Handles communication from content process to the background process.
 */
export default class BackgroundIPC
{
  static cacheStore(action, data)
  {
    const command = BackgroundCommands.CACHE_STORE;
    return HandlePromise.handlePromiseMessage({ command, action, data });
  }

  static isFrameBlocked(url)
  {
    const command = BackgroundCommands.IS_FRAME_BLOCKED;
    return HandlePromise.handlePromiseMessage({ command, url });
  }

  static makeMTIRequest(requestData, referer)
  {
    const command = BackgroundCommands.TI_REQUEST;
    const request = { command, requestData, referer };
    return HandlePromise.handlePromiseMessage(request);
  }

  static executeCommand(commandId, params)
  {
    const command = BackgroundCommands.EXECUTE_COMMAND;
    extension.runtime.sendMessage({ command, commandId, params });
  }

  static focusOrCreateTab(url)
  {
    const command = BackgroundCommands.FOCUS_OR_CREATE_TAB;
    extension.runtime.sendMessage({ command, url });
  }

  static closeTab()
  {
    const command = BackgroundCommands.REMOVE_TAB;
    extension.runtime.sendMessage({ command });
  }

  static whitelist(action, type, data)
  {
    const command = BackgroundCommands.WHITELIST;
    return HandlePromise.handlePromiseMessage({ action, command, type, data });
  }

  static getPopupData()
  {
    const command = BackgroundCommands.GET_POPUP_DATA;
    return HandlePromise.handlePromiseMessage({ command });
  }

  static getSettingsData()
  {
    const command = BackgroundCommands.GET_SETTINGS_DATA;
    return HandlePromise.handlePromiseMessage({ command });
  }

  static searchAnnotation(action, data)
  {
    const command = BackgroundCommands.SEARCH_ANNOTATION;
    return HandlePromise.handlePromiseMessage({ action, data, command });
  }

  static socialMediaAnnotation(action, data)
  {
    const command = BackgroundCommands.SOCIAL_MEDIA_ANNOTATION;
    return HandlePromise.handlePromiseMessage({ action, data, command });
  }

  static async getBkGlobals(bIncludeSearchEngines = false)
  {
    return new Promise((resolve) =>
    {
      const command = BackgroundCommands.GET_BK_GLOBALS;
      const promise = HandlePromise.handlePromiseMessage({ command, bIncludeSearchEngines });
      promise.then((val) => { resolve(JSON.parse(val)); });
    });
  }

  static viewSiteReport(url = null, showInNewTab = false)
  {
    const command = BackgroundCommands.VIEW_SITE_REPORT;
    extension.runtime.sendMessage({ command, url, showInNewTab });
  }

  static getTypoSquattingData(domain)
  {
    const command = BackgroundCommands.GET_TYPOSQUATTING_DATA;
    return HandlePromise.handlePromiseMessage({ command, domain });
  }

  static getPlaceholderText(id)
  {
    const command = BackgroundCommands.PLACEHOLDER_TEXT;
    return HandlePromise.handlePromiseMessage({ command, id });
  }

  static setViewPort(x, y)
  {
    const command = BackgroundCommands.SET_VIEWPORT;
    extension.runtime.sendMessage({ command, x, y });
  }

  static sendTelemetry(telemetry)
  {
    const command = BackgroundCommands.SEND_TELEMETRY;
    extension.runtime.sendMessage({ command, telemetry });
  }

  static anyBlockedIframes(frameUrls)
  {
    const command = BackgroundCommands.ANY_BLOCKED_IFRAMES;
    return HandlePromise.handlePromiseMessage({ command, frameUrls });
  }

  static anyBlockedCryptoScripts()
  {
    const command = BackgroundCommands.ANY_BLOCKED_CRYPTOSCRIPTS;
    return HandlePromise.handlePromiseMessage({ command });
  }

  static resetSettings()
  {
    const command = BackgroundCommands.RESET_SETTINGS;
    extension.runtime.sendMessage({ command });
  }

  static sendEngineStat(engine)
  {
    extension.runtime.sendMessage({ command: BackgroundCommands.UPDATE_ENGINE_STATS, engine });
  }

  static contentHandler(message)
  {
    const command = BackgroundCommands.CONTENT_HANDLER;
    extension.runtime.sendMessage({ command, message });
  }

  static getTabData(payload = {})
  {
    const command = BackgroundCommands.GET_TAB_DATA;
    const request = { command, ...payload };
    return HandlePromise.handlePromiseMessage(request);
  }

  static isWhitelisted(url)
  {
    const command = BackgroundCommands.IS_WHITELISTED;
    const request = { command, url };
    return HandlePromise.handlePromiseMessage(request);
  }

  static getExtensionStatus(id)
  {
    return HandlePromise.handlePromiseMessage({ command: BackgroundCommands.GET_EXTENSION_STATUS, extension_id: id });
  }

  static unblockAllIframes()
  {
    const command = BackgroundCommands.UNBLOCK_ALL_IFRAMES;
    extension.runtime.sendMessage({ command });
  }

  static updateRatDetectionShowingFlag(showed)
  {
    const command = BackgroundCommands.UPDATE_RAT_DETECTION_SHOWING_FLAG;
    extension.runtime.sendMessage({ command, showed });
  }

  /**
   * Send a message to the background to retrieve the results from Search Suggestion for the search term
   *
   * @param {String} searchTerm
   */
  static getSearchSuggest(searchTerm)
  {
    const command = BackgroundCommands.SEARCH_SUGGEST;
    const request = { command, searchTerm };
    return HandlePromise.handlePromiseMessage(request);
  }

  static resetNativeSetting(setting)
  {
    const command = BackgroundCommands.RESET_NATIVE_SETTING;
    extension.runtime.sendMessage({ command, setting });
  }

  /**
   * Save form info (logging info)
   * @param {string} username - username entered in the form.
   * @param {string} hostname - hostname of the page the form was submitted on.
   */
  static saveFormInfo(username, hostname)
  {
    const command = BackgroundCommands.SAVE_FORM_INFO;
    extension.runtime.sendMessage({ command, username, hostname });
  }

  /**
   * Send message to the backend to save login from multi-step detection
   * @param {Object} data - Object containing the neccesary info from the form.
   * @param {string} data.username - (optional) username entered into the form. Only supplied if login not complete, ie first step of multistep form.
   * @param {string} data.hostanme - hostname of the page the form was submitted on.
   * @param {Boolean} completeLogin - Whether or not the multistep login process is completed or not.
   */
  static saveMultiStepLogin(data, completeLogin)
  {
    const command = BackgroundCommands.SAVE_MULTISTEP_LOGIN;
    extension.runtime.sendMessage({ command, data, completeLogin });
  }

  static getFormInfoCache()
  {
    const command = BackgroundCommands.GET_FORM_INFO_CACHE;
    return HandlePromise.handlePromiseMessage({ command });
  }

  static clearFormInfoCache()
  {
    const command = BackgroundCommands.CLEAR_FORM_INFO_CACHE;
    extension.runtime.sendMessage({ command });
  }

  /**
   * Update list of emails to be excluded from DWS offering.
   * The email is whitelisted either because the user said not to offer it again, or because the user
   * added it to be monitored in the web portal.
   * @param {*} email
   */
  static updateDWSWhitelist(email)
  {
    const command = BackgroundCommands.UPDATE_DWS_WHITELIST;
    extension.runtime.sendMessage({ command, email });
  }

  static getCachedDWSInfo(email)
  {
    const command = BackgroundCommands.GET_CACHED_DWS_INFO;
    const request = { command, email };
    return HandlePromise.handlePromiseMessage(request);
  }

  static clearCachedDWSInfo(email)
  {
    const command = BackgroundCommands.CLEAR_CACHED_DWS_INFO;
    extension.runtime.sendMessage({ command, email });
  }

  static updateDWSShown(email)
  {
    const command = BackgroundCommands.UPDATE_DWS_SHOWN;
    extension.runtime.sendMessage({ command, email });
  }

  static getAPSDetails()
  {
    const command = BackgroundCommands.GET_APS_DETAILS;
    return HandlePromise.handlePromiseMessage({ command });
  }

  static signUpFormDetected()
  {
    const command = BackgroundCommands.SIGN_UP_FORM_DETECTED;
    extension.runtime.sendMessage({ command });
  }

  static updateBkNativeSettings(name, value)
  {
    const command = BackgroundCommands.UPDATE_BK_NATIVE_SETTINGS;
    extension.runtime.sendMessage({ command, name, value });
  }

  static launchIDPSLogin()
  {
    const command = BackgroundCommands.LAUNCH_IDPS_LOGIN;
    extension.runtime.sendMessage({ command });
  }

  static showSidebarMain()
  {
    const command = BackgroundCommands.SHOW_SIDEBAR_MAIN;
    extension.runtime.sendMessage({ command });
  }

  static setFFPolicyCollection({ personal, activity })
  {
    const command = BackgroundCommands.SET_FF_POLICY_COLLECTION;
    extension.runtime.sendMessage({ command, personal, activity });
  }

  static setFFPolicyLastShown()
  {
    const command = BackgroundCommands.SET_FF_POLICY_LAST_SHOWN;
    extension.runtime.sendMessage({ command });
  }

  // This will broadcast a sendMessage to all page owned by us
  // For example, to all foreground pages. A message will be sent to
  // background as well but it will not be processed
  static broadcastToForeground(payload)
  {
    const command = ContentCommands.BROADCAST_TO_FOREGROUND;
    extension.runtime.sendMessage({ command, payload });
  }

  static getFDWeights()
  {
    const command = BackgroundCommands.GET_FD_WEIGHTS;
    return HandlePromise.handlePromiseMessage({ command });
  }

  static getFDExceptions()
  {
    const command = BackgroundCommands.GET_FD_EXCEPTIONS;
    return HandlePromise.handlePromiseMessage({ command });
  }

  static getFDCrfParams()
  {
    const command = BackgroundCommands.GET_FD_CRF_PARAMS;
    return HandlePromise.handlePromiseMessage({ command });
  }
}
