// Component
import ShadowDomContainer from '@extension/components/shadow_dom_container';
import InteractiveBalloon from '@extension/components/interactive_balloon';

const balloonMessageStyles = {
  DEFAULT: {
    backgroundColor: 'transparent',
    border: 'none',
    bottom: '15px',
    display: 'block',
    margin: '0px',
    opacity: '0',
    padding: '0px',
    position: 'fixed',
    right: '65px',
    zIndex: '2147483646',
  },

  HIDE: {
    opacity: '0',
  },

  SHOW: {
    backgroundColor: 'transparent',
    opacity: '1',
  },
};

export default class BalloonMessageContainer extends ShadowDomContainer
{
  constructor(customStyles = {}, waImageSecret)
  {
    balloonMessageStyles.DEFAULT = { ...balloonMessageStyles.DEFAULT, ...customStyles };
    super(balloonMessageStyles);
    this.interactiveBalloon = new InteractiveBalloon(waImageSecret);
  }

  async init(template, componentData)
  {
    const success = await this.initContainer(template, componentData);
    await this.interactiveBalloon.init(this.shadowRoot);
    return success;
  }
}
