import { extension } from '@extension/common/webextension';
import { ExternalCommands } from '@extension/common/commands';
import { Logger } from '@extension/common/logger';

export default class ExternalMsgDispatcher
{
  isvalidRequestType(requestType)
  {
    let valid = false;
    for (const type in ExternalCommands)
    {
      if (ExternalCommands[type] === requestType)
      {
        valid = true;
      }
    }

    return valid;
  }

  validateMsg(requestType, payload)
  {
    if (
      requestType === undefined
      || payload === undefined
      || !this.isvalidRequestType(requestType)
    )
    {
      Logger.error('Invalid msg to be sent externally');
      return false;
    }
    return true;
  }

  sendExtnMsg(extnId, message, cb)
  {
    const defaultCb = (resolve, reject, data) =>
    {
      if (extension.runtime.lastError) reject(extension.runtime.lastError.message);
      resolve(data);
    };

    return new Promise((resolve, reject) =>
    {
      const { request_type: requestType, payload } = message;
      if (!this.validateMsg(requestType, payload))
      {
        reject(Error('sendExtnMsg: Invalid Message'));
      }

      extension.runtime.sendMessage(extnId, message, (data) =>
      {
        if (typeof cb === 'function') cb(resolve, reject, data);
        else defaultCb(resolve, reject, data);
      });
    });
  }
}
