import { ContentCommands, BalloonMessageCommands } from '@extension/common/commands';
import BalloonMessageUIHandler from '@extension/scripts/ui_handler/balloon_message_ui_handler';
import ScriptBase from '@extension/scripts/content_base';

export default class ContentAPSToast extends ScriptBase
{
  constructor()
  {
    super(ContentCommands.PING_CONTENT_APS_TOAST);
    this.balloonMessageUIHandler = new BalloonMessageUIHandler();
  }

  main()
  {
    // Show APS Toast message
    this.balloonMessageUIHandler.processMessage({
      subcommand: BalloonMessageCommands.ADVANCED_PROTECTION_SIGNAL_TOAST,
      componentData: {}
    });
  }
}

const contentAPSToast = new ContentAPSToast();
contentAPSToast.main();
