import { TelemetryTerms } from '@extension/telemetry/common_telemetry';
import { TelemetryMessageModifiers } from '@extension/telemetry/telemetry_message_modifiers';
import { BackgroundGlobals } from '@extension/background/background_globals';
import BackgroundIPC from '@extension/common/background_ipc';
import { onDOMContentReady } from '@extension/utilities/utils';

export default class ContextTelemetry
{
  /* Registers the telemetry and fetches the respective messages based on the mapped 'identifiers'.
     - Sends telemetry if associated with a User action event for a few respective cases.
     - Else sends telemetry based on the 'sendTelemetry' attribute value for a few respective cases.
     - Or else sends telemetry when the document is ready for a few respective cases.
  */
  static sendTelemetryMessages(options, handler)
  {
    const { sendTelemetry } = handler.trigger;
    const triggerAction = handler.trigger.action;
    const permittedActionList = [TelemetryTerms.ACTION.CLICK]; // All non-keyPress user actions

    if (handler.trigger.selector && handler.trigger.action)
    {
      if (options.containerMeta)
      {
        const selectedTag = options.containerMeta.query(handler.trigger.selector);
        if (selectedTag)
        {
          selectedTag.addEventListener(handler.trigger.action, sendMessages);
        }
      }
      else
      {
        onDOMContentReady(() =>
        {
          const selectedTag = document.querySelector(handler.trigger.selector);
          if (selectedTag)
          {
            selectedTag.addEventListener(handler.trigger.action, sendMessages);
          }
        });
      }
    }
    else if (sendTelemetry || (handler.trigger.state === options.state))
    {
      sendMessages();
    }

    /**
     * Function handling the sending of telemetry, either from content or background script.
     * @param {Object} event - Event object if invoked by an event listener. Can also be undefined if invoked directly by us.
     */
    function sendMessages(event)
    {
      handler.messages.forEach((spec) =>
      {
        const telemetryMessage = { ...spec.template };
        // send telemetry only when its loaded or when its loaded with count != 0 (SMA Reputation)
        const shouldSendMessage = ((sendTelemetry) && ((telemetryMessage.count !== undefined) ? (telemetryMessage.count !== 0) : true));

        /* send telemetry when the key pressed is in WHITELISTEDKEYS array (found in 'options' JSON)
           or if the user action is a click event */
        let worthyAction = permittedActionList.includes(triggerAction);
        if (triggerAction === TelemetryTerms.ACTION.KEYDOWN && options.whitelistedKeys)
        {
          const eventCode = event.keyCode;
          worthyAction = options.whitelistedKeys.includes(eventCode);
        }

        if (shouldSendMessage || worthyAction)
        {
          if (options.isFromBackground)
          {
            BackgroundGlobals.messageDispatcher.sendTelemetry(JSON.stringify(telemetryMessage));
          }
          else
          {
            BackgroundIPC.sendTelemetry(JSON.stringify(telemetryMessage));
          }
        }
      });
    }
  }

  /* Accepts the paramters required for detecting the respective telemetry */
  static handle(options)
  {
    const messageModifiers = new TelemetryMessageModifiers(options);
    const telemetryHandlers = messageModifiers.fetchTelemetryHandlers(options.identifier);
    telemetryHandlers.forEach((handler) =>
    {
      this.sendTelemetryMessages(options, handler);
    });
  }
}
